.header__container {
    position: relative;
    height: 100%;
    background: -webkit-linear-gradient(60deg, #4ECDC4, #FF6F6D 99%);
    background: -o-linear-gradient(60deg, #4ECDC4, #FF6F6D 99%);
    background: linear-gradient(30deg, #4ECDC4, #FF6F6D 99%);
    display: -ms-flexbox;
    display: flex;
}

.header__content {
    margin: auto;
    text-align: center;
}

.header__banner {
    margin: 15px;
    font-size: 3em;
    background-color: #FFFFFF;
    border-radius: 20px;
    padding: 20px 50px;
    color: #FF6B6B;
}

.header__subtext {
    font-size: 1em;
    color: #292F36;
    width: 100%;
    line-height: 2em;
    background-color: #FFFFFF;
    -webkit-transform: rotate(2deg);
        -ms-transform: rotate(2deg);
            transform: rotate(2deg);
}

.header__snsLinks {
    margin-top: 2em;
}

.header__snsLinks a, .header__snsLinks a:visited {
    color: #FFFFFF;
    font-size: 1.5em;
}


